/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
// Widget Area Default
.widget-area-container {
    width: 300px;
}
// Button 
.theme-button {
    color: black;
    width: 150px;
    margin: 10px 0;

    a {
        color: black;
        text-decoration: none;

        p {
            text-transform: uppercase;
            padding: 10px;
            text-align: center;
            border: 1px solid black;
        }

        *:hover {
            background: $base_theme_green;
            border: 1px solid rgba($color: #000000, $alpha: 0);
            color: $base_journal;
        }
    }
}
/* Front page */
.width-restriction {
    margin: 0 auto;
    width: $base-page-width;
}
.site-main {
    margin-top: 3rem
}

.site-wrapper {
    width: 100%;
    .site-content {
        display: grid;
        grid-template-columns: 3fr 1fr;
        grid-gap: 0 4em;

        .content-area {
            grid-column-start: 1;
        }
        
        .widget-area {

            border-left: 1px solid $border-gray;
            grid-column-start: 0;
        }
    }
}
// Journal Page
.journal-entry {
    
    .journal-thumbnail {
        position: relative;
    }

    .journal-title-wrapper {
        max-width: 600px;
        text-transform: uppercase;
        position: absolute;
        margin-top: 2em;
        background: $base_theme_green;
        z-index: 1;

       a {

        color: $base_journal;
        text-decoration: none;
        *:hover {
            color: $color_link-hover_navigation;
        }
        .journal-title {
            margin: 1.2rem;
            max-width: 90%;
            line-height: 1.2;
        }

       } 
        
    }
    
    .journal-info {
        
        color: $base-journal;
        background: $base_theme_green;
        position: absolute;
        z-index: 1;
        padding: .4rem .8rem;
        margin-top: -3.15em;
        margin-left: 425.5px;
    }
}
// Find Us/ Contact page
.entry-content {

    max-width: 700px;
    label {
        font-size: 1.25em;
        text-transform: uppercase;
    }
    input , textarea{
        background: $color__background-pre;
        border: 1px solid $border-gray;
    }
    .required {
        color: $base_theme_orange;
    }

    .wpcf7-submit {
        text-transform: uppercase;
        font-size: 16px;
        background: $base_theme_green;
        text-shadow: none;
        color: $base_white;
        border: none;
        border-radius: 0;
        box-shadow: none;   
        padding: 10px 20px;

        &:hover  {
            background: $base_theme_green_darken;
            
        }
    }

}